import {row, col, css} from '../utils'

class Block {
    constructor(value, options) {
        this.value = value
        this.options = options
    }
    
    toHTML() {
        throw new Error('–ú–µ—Ç–æ–¥ toHTML –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å —Ä–µ–∞–ª–∏–∑–æ–≤–∞–Ω')
    }
}

// –¢–µ–ø–µ—Ä—å, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤—ã–π –±–ª–æ–∫, –Ω–∞–º –Ω–µ –Ω—É–∂–Ω–æ –ø–∏—Å–∞—Ç—å –Ω–æ–≤—É—é —Ñ—É–Ω–∫—Ü–∏—é. –ù—É–∂–Ω–æ –ø—Ä–æ—Å—Ç–æ –¥–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤—ã–π –∫–ª–∞—Å—Å, –Ω–∞—Å–ª–µ–¥—É—é—â–∏–π –æ—Ç –±–ª–æ–∫–∞ value –∏ options

export class TitleBlock extends Block {
    constructor(value, options) {
        super(value, options)
    }
    
    toHTML() {
        const {tag = 'h1', styles} = this.options
        // // –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è –¥–ª—è —Ç—ç–≥–∞ –∑–∞–≥–æ–ª–æ–≤–∫–∞, ?? - –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è, –µ—Å–ª–∏ —Ç—ç–≥ –Ω–µ —É–∫–∞–∑–∞–Ω
        // const tag = block.options.tag ?? 'h1'
        // const styles = block.options.styles 
        return row(col(`<${tag}>${this.value}</${tag}>`), css(styles))
    }
}

export class ImageBlock extends Block {
    constructor(value, options) {
        super(value, options)
    }
    
    toHTML() {
        const {imageStyles: is, alt = '', styles} = this.options
        return row(`<img src = "${this.value}" alt = "${alt}" style = "${css(is)}">`, css(this.options.styles))    
    }
}

export class ColumnsBlock extends Block {
    constructor(value, options) {
        super(value, options)
    }
    
    toHTML() {
        let column = this.value.map(col).join('')
        return row(column, css(this.options.styles))   
    }
}

export class TextBlock extends Block {
    constructor(value, options) {
        super(value, options)
    }
    
    toHTML() {
        return row(col(`<p>${this.value}</p>`), css(this.options.styles))
    }
}