import {block} from '../utils'
import {TextBlock, TitleBlock} from './blocks'

export class Sidebar {
    constructor(selector, updateCallback) {
        this.$element = document.querySelector(selector)
        this.update = updateCallback
        
        this.init()
    }
    
    init() {
        this.$element.insertAdjacentHTML('afterbegin', this.template)
        // –ß—Ç–æ–±—ã –Ω–µ —Ç–µ—Ä—è—Ç—å –∫–æ–Ω—Ç–µ–∫—Å—Ç –≤—ã–∑–æ–≤–∞ this - –ø—Ä–∏–≤—è–∑—ã–≤–∞–µ–º –µ–≥–æ –º–µ—Ç–æ–¥–æ–º bind
        this.$element.addEventListener('submit', this.add.bind(this))
    }
    
    get template() {
        return [
            block('text'),
            block('title')
        ].join('')
    }
    
    // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è –¥–∞–Ω–Ω—ã—Ö —Å–æ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∏ –æ—Ç–º–µ–Ω—ã –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∫–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
    add(event) {
        event.preventDefault()
        
        const type = event.target.name,
              value = event.target.value.value,
              styles = event.target.styles.value
              
        // –ü–µ—Ä–µ–ø–∏—Å–∞—Ç—å —ç—Ç–æ—Ç –∫—É—Å–æ–∫ –∫–æ–¥–∞, —Ç–∞–∫ –∫–∞–∫ –æ–Ω —É–ø—Ä–æ—â–µ–Ω–∏ —Å–¥–µ–∞–Ω —Ç–æ–ª—å–∫–æ –ø–æ–¥ 2 —Ç–∏–ø–∞ –±–ª–æ–∫–æ–≤: —Ç–µ–∫—Å—Ç –∏ –∑–∞–≥–æ–ª–æ–≤–æ–∫. –î–æ–±–∞–≤–∏—Ç—å —Ñ—É–Ω–∫—Ü–∏—é?
        const newBlock = type === 'text' 
            ? new TextBlock(value, {styles})
            : new TitleBlock(value, {styles})  
            
        this.update(newBlock)
        
        // –û—á–∏—Å—Ç–∫–∞ —Ñ–æ—Ä–º—ã
        event.target.value.value = ''
        event.target.styles.value = ''
    }  
}