export class Site {
    constructor(selector) {
    // $ - —ç—Ç–∏–º –∑–Ω–∞–∫–æ–º –æ–±–æ–∑–Ω–∞—á–∞—é—Ç—Å—è —ç–ª–µ–º–µ–Ω—Ç—ã DOM-–¥–µ—Ä–µ–≤–∞
        this.$element = document.querySelector(selector)
    }
    
    render(model) {
        // –ß—Ç–æ–±—ã –Ω–µ –¥—É–±–ª–∏—Ä–æ–≤–∞–ª—Å—è –≤–µ—Å—å HTML –ø—Ä–∏–¥–æ–±–∞–≤–ª–µ–Ω–∏–∏ –Ω–æ–≤–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞
        this.$element.innerHTML = ''
        // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞
        model.forEach(block => {
            this.$element.insertAdjacentHTML('beforeend', block.toHTML())
        })
    }
}